*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container{
    width: 100%;
    height: auto;
    min-height: 100vh;
    section.alphaBets{
        width: 100%;
        height: 280px;
        border: 1px solid black;
        .alphaDiv{
            width: 90%;
            height: 100px;
           
            margin: 20px auto;
            overflow: scroll;
            .scrollDiv{
                width: calc(60 * 26)px;
                height:60px;
                display: inline-flex;
                align-items: center;
             
            button{
                width: 40px;
                height: 40px;
                background-color: #2b92e4;
                outline: none;
                border: none;
                margin-left: 20px;
                cursor: pointer;
                border-radius: 10px;
                font-size: 1.2rem;
                color: white;
            }
        }
    }
    .arrowInt{
        display: flex;
        justify-content: space-evenly;
    }
    .submitDiv{
        text-align: center;
        margin-top: 40px;
    }
    }
}
.container{
    section.dragSec{
        width: 100%;
        height: 150px;
        margin-top: 50px;
        border: 1px solid black;
        h4{
            font-size: 1.3rem;
            font-weight: 100;
            padding-left: 50px;
        }
        .dragDiv{
            width: 100%;
            height: 70%;
            margin-top: 20px;
            padding-left: 30px;
            display: flex;
            .btnbox{
               width: 55px;
               height: 45px;
               position: relative;
               margin-left: 30px;
             button{
                 position: absolute;
                 top: 0;
                 left: 0px;
                 margin-left: 0;
                 width: 40px;
    height: 40px;
             }
               span{
                   position: absolute;
                   top: -15px;
                   right: 0;
                   cursor: pointer;
               }
               
            }
        }
    }
}
button{
    width: 40px;
    height: 40px;
    background-color: #2b92e4;
    outline: none;
    border: none;
    margin-left: 20px;
    cursor: pointer;
    border-radius: 10px;
    font-size: 1.2rem;
    color: white;
}
button#gt,button#lt,button.orange{
    background-color: #b28e44;
}
button#int,button.green{
    background-color: #52baa0;
    width: 50px;
}
button#submt{
    width: 100px;
    height: 40px;
    background-color: #f07642;
}

.container{
    section.result{
        width: 100%;
        height: 200px;
        border: 1px solid black;
        margin-top: 50px;
        margin-bottom: 50px;
        h1{
            font-size: 2.3rem;
            text-align: center;
        }
        .resultDiv{
            width: 100%;
            height: 150px;
            padding-left: 50px;
            padding-top: 20px;
            display: flex;
            p{
                font-size: 1.2rem;
            }
            .resultShow{
               width: auto;
               height: 100%;
               padding-left: 20px;
            }
        }
    }
}